.intro {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.intro-bg {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.intro-bg-img {
    width: 100%;
    height: 100vh;
    position: relative;
    object-fit: cover;
    top:0;
}

.intro-title {
    position: absolute;
    top: 75%;
    text-align: center;
    color: beige;
}

.intro-title-subject {
    font-weight: 400;
    font-family: "Nirmala UI";
}

.intro-title-name {
    font-family: "Dancing Script", cursive;
    font-weight: 200;
    font-size: 45px;
}

.intro-title-date {
    font-size: 15px;
}

.intro-bg-overlay {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    position: absolute;
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 1));
    opacity: 0.5;
    top: 0;
}

@media screen and (max-width: 480px) {
    .intro-title {

    }
}